.glasswindow-wrapper {
  position: relative;
  color: #5e5c5c;
  top: 150px;
  left: 400px;
  border-radius: 8px;
  box-shadow: 10px 10px 2px rgba(0, 0, 0, 0.5);
  backdrop-filter: blur(13px);
  padding: 50px;
  box-sizing: border-box;
  font-size: 20px;
}
.glasswindow-wrapper .x {
  position: absolute;
  right: 20px;
  top: 20px;
  color: #958F8F;
}
.glasswindow-wrapper h2 {
  font-size: 30px;
}
.glasswindow-wrapper::before {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0px;
  left: 0px;
  border-radius: 8px;
  filter: blur(2px);
  border: 4px solid #6e6565;
}
