.CoolBackground {
  width: 100vw;
  height: 100vh;
  position: absolute;
  overflow: hidden;
  z-index: -1;
}
.CoolBackground .divblock {
  position: absolute;
  height: 12.5vh;
  width: 6.25vw;
  opacity: 0;
}

.content {
	width: 600px;
	height: 400px;
	position: absolute;
	color: black;
	top: 50px;
	left: 30px;
	border-radius: 8px;
	box-shadow: 10px 10px 2px  rgba(0,0,0,0.5);
	-webkit-backdrop-filter: blur(13px);
	        backdrop-filter: blur(13px);
	padding: 50px;
	box-sizing: border-box;
}
.content::before{
	content: "";
	width: 600px;
	height: 400px;
	position: absolute;
	top: 0px;
	left: 0px;
	border-radius: 8px;
	-webkit-filter: blur(2px);
	        filter: blur(2px);
	border: 4px solid rgb(110, 101, 101);


}
.content h1 {
	text-align: center;
	margin-bottom: 20px;
	font-size: 30px;
	font-weight: bold;
}

.content p {
	text-indent: 2em;
	line-height: 1.7;
	font-size: 25px;
	font-weight: bold;
}
.glasswindow-wrapper {
  position: relative;
  color: #5e5c5c;
  top: 150px;
  left: 400px;
  border-radius: 8px;
  box-shadow: 10px 10px 2px rgba(0, 0, 0, 0.5);
  -webkit-backdrop-filter: blur(13px);
          backdrop-filter: blur(13px);
  padding: 50px;
  box-sizing: border-box;
  font-size: 20px;
}
.glasswindow-wrapper .x {
  position: absolute;
  right: 20px;
  top: 20px;
  color: #958F8F;
}
.glasswindow-wrapper h2 {
  font-size: 30px;
}
.glasswindow-wrapper::before {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0px;
  left: 0px;
  border-radius: 8px;
  -webkit-filter: blur(2px);
          filter: blur(2px);
  border: 4px solid #6e6565;
}

/* 全局样式 */
* {
    box-sizing: border-box;
  }
  *:before,
  *:after {
    box-sizing: border-box;
  }
  body, div, dl, dt, dd, ul, ol, li, h1, h2, h3, h4, h5, h6, pre, code, form, fieldset, legend, input, button, textarea, p, blockquote, th, td {
      margin: 0;
      padding: 0;
  }
  body {
      background: #fff;
      color: #555;
      /* font-size: 14px; */
      font-family: "Arial", "Microsoft YaHei", "黑体", "宋体", sans-serif;
  }
  td, th, caption {
      font-size: 14px;
  }
  h1, h2, h3, h4, h5, h6 {
      font-weight: normal;
      font-size: 100%;
  }
  address, caption, cite, code, dfn, em, strong, th, var {
      font-style: normal;
      font-weight: normal;
  }
  a {
      color: #555;
      text-decoration: none;
  }
  a:hover {
      text-decoration: underline;
  }
  img {
      border: none;
      vertical-align: middle;
  }
  ol, ul, li {
      list-style: none;
  }
  input, textarea, select, button {
      font: 14px "Arial", "Microsoft YaHei", "黑体", "宋体", sans-serif;
  }
  table {
      border-collapse: collapse;
  }
  html {
      height: 100%;
      /* overflow-y: scroll; */
  }
  .clearfix:before,
  .clearfix:after {
      content: " ";
      display: inline-block;
      height: 0;
      clear: both;
      visibility: hidden;
  }
  .clearfix {
      *zoom: 1;
  }
  
  /*公共类*/
  .fl {
      float: left
  }
  .fr {
      float: right
  }
  .al {
      text-align: left
  }
  .ac {
      text-align: center
  }
  .ar {
      text-align: right
  }
  .hide {
      display: none
  }
  
