.content {
	width: 600px;
	height: 400px;
	position: absolute;
	color: black;
	top: 50px;
	left: 30px;
	border-radius: 8px;
	box-shadow: 10px 10px 2px  rgba(0,0,0,0.5);
	backdrop-filter: blur(13px);
	padding: 50px;
	box-sizing: border-box;
}
.content::before{
	content: "";
	width: 600px;
	height: 400px;
	position: absolute;
	top: 0px;
	left: 0px;
	border-radius: 8px;
	filter: blur(2px);
	border: 4px solid rgb(110, 101, 101);


}
.content h1 {
	text-align: center;
	margin-bottom: 20px;
	font-size: 30px;
	font-weight: bold;
}

.content p {
	text-indent: 2em;
	line-height: 1.7;
	font-size: 25px;
	font-weight: bold;
}